<template>
  <div class="descripcion-content" v-show="boolVista">
    <div class="container">
      <div v-if="finish">
        <div class="card row  pt-32">
          <div class="col-lg-12 text-center mt-2">
            <h4 style="color: #0078cf">Se gener la solicitud Nro. {{ this.idSolicitud }}</h4>

          </div>
          <div class="col-sm-10"></div>

          <div class="mt-5"></div>
          <div class="form-inline align-self-center mt-2">
            <div class="mx-lg-5 mt-2 text-center ">
              <img src="../../assets/tengaEnCuenta.png" height="55" width="55"/><a :href="urlPdfDescargar" target="_blank"><u><b>Descargar en formato PDF</b></u></a>
              <div class="form-control-plaintext" />
            </div>
            <div class="form-group mx-sm-3 mb-2">
              <label class="sr-only">Password</label>
              <input type="text" v-model="enailEnviar" class="form-control" style="width: 350px" placeholder="Ingrese su correo para el envo de la constancia"/>
            </div>
            <button v-on:click="enviarCorreo()" class="btn btn-default">Enviar copia</button>
          </div>
          <div class="mt-4"></div>

        </div>
      </div>

      <div v-else="finish">
        <div class="card row  pt-32">
          <div class="top-title">
            <div class="col-lg-12">
              <h3 class="titulo-form-regitroAcceso  pb-32">Formulario de Registro de Solicitudes de Acceso a la
                Informacin Pblica</h3>
            </div>
          </div>

          <div class="col-12">
            <b-card no-body style="border:none;outline:none !important;box-shadow: none;">
              <div class="form-group col-lg-12">
                <div class="row d-flex justify-content-between text-align-left">
                  <div class="col-lg-3" style="padding-left:0px">
                    <a style="text-align:left"><b>Fecha de registro: </b> {{ customFormatter(this.date) }}</a>
                  </div>
                  <div class="col-lg-3" style="padding-left:0px">
                    <a style="text-align:left"><b>Nro. Solicitud: </b> {{ this.solicitud }} - {{ this.anho }}</a>
                  </div>
                </div>
              </div>
            </b-card>
          </div>

          <div class="col-12">
            <div class="alert alert-info mt-3" role="alert" v-html="descripcionTipoTramite"></div>
          </div>

          <div class="col-12">
            <b-card header="DATOS DEL SOLICITANTE">
              <div class="row">
                <div class="col-12">
                  <div class="row">
                    <div class="form-group col-12  col-lg-4">
                      <label style="margin-top:10px;padding-right:5px">Tipo de Documento(*): </label>
                      <b-form-radio-group v-model="tipoDoc" style="display: inline-block;">
                        <b-form-radio name="radio-size" v-model="rdTipoDoc" value="31" size="sm"
                                      @change="rdPersona=109">DNI
                        </b-form-radio>
                        <b-form-radio name="radio-size" v-model="rdTipoDoc" value="33" size="sm"
                                      @change="rdPersona=109">CE
                        </b-form-radio>
                        <b-form-radio name="radio-size" v-model="rdTipoDoc" value="32" size="sm">RUC</b-form-radio>
                      </b-form-radio-group>
                    </div>
                    <div class="form-group col-12 col-lg-8">
                      <b-input-group>
                        <b-form-input v-if="rdTipoDoc==31" placeholder="Ingresa tu documento" v-model="nroDoc"
                                      @input="v => {nroDoc = v.toUpperCase()}" maxlength="8"
                                      type="search"></b-form-input>
                        <b-form-input v-if="rdTipoDoc==32" placeholder="Ingresa tu documento" v-model="nroDoc"
                                      @input="v => {nroDoc = v.toUpperCase()}" maxlength="11"
                                      type="search"></b-form-input>
                        <b-form-input v-if="rdTipoDoc==33" placeholder="Ingresa tu documento" v-model="nroDoc"
                                      @input="v => {nroDoc = v.toUpperCase()}" maxlength="12"
                                      type="search"></b-form-input>
                      </b-input-group>
                    </div>
                  </div>
                  <div class="row">
                    <div class="form-group col-sm-4">
                      <label style="margin-top:10px;padding-right:5px">Tipo de Persona(*): </label>
                      <b-form-radio-group v-model="tipoPer" style="display: inline-block;">
                        <b-form-radio name="radio-size" v-model="rdPersona" :value=109 size="sm">Natural</b-form-radio>
                        <b-form-radio name="radio-size" v-model="rdPersona" :value=110 size="sm"
                                      :disabled="(rdTipoDoc==31 || rdTipoDoc==33)">Jurdica
                        </b-form-radio>
                      </b-form-radio-group>
                    </div>

                    <template v-if="rdPersona">
                      <div class="col-sm-8" v-if="rdPersona==109">
                        <div class="row">
                          <div class="form-group col-sm-4">
                            <b-input-group>
                              <b-form-input placeholder="Nombre(s)" v-model="nombre"
                                            @input="v => {nombre = v.toUpperCase()}"></b-form-input>
                            </b-input-group>
                          </div>
                          <div class="form-group col-sm-4">
                            <b-input-group>
                              <b-form-input placeholder="Ape. paterno" v-model="aPaterno"
                                            @input="v => {aPaterno = v.toUpperCase()}"></b-form-input>
                            </b-input-group>
                          </div>
                          <div class="form-group col-sm-4">
                            <b-input-group>
                              <b-form-input placeholder="Ape. materno" v-model="aMaterno"
                                            @input="v => {aMaterno = v.toUpperCase()}"></b-form-input>
                            </b-input-group>
                          </div>
                        </div>
                      </div>
                      <div class="form-group col-sm-8" v-if="rdPersona==110">
                        <div class="row">
                          <div class="form-group col-sm-12">
                            <b-input-group>
                              <b-form-input placeholder="Razn Social (*):" v-model="rSocial"
                                            type="search"></b-form-input>
                            </b-input-group>
                          </div>
                        </div>
                      </div>
                    </template>
                  </div>
                  <div class="row">
                    <div class=" col-12  col-lg-4">
                      <div>
                        <div><label>Domicilio Legal(*):</label></div>
                        <b-form-input v-model="domicilio" @input="v => {domicilio = v.toUpperCase()}"></b-form-input>
                      </div>
                      <div class="form-group ">
                        <b-input-group>
                        </b-input-group>
                      </div>
                    </div>
                    <div class="col-12  col-lg-8">
                      <div class="row">
                        <div class="col-12  col-lg-4" v-if="mapListaDepartamento">
                          <div><label>Departamento:</label></div>
                          <model-select
                            :options="mapListaDepartamento"
                            v-model="itemDepartamento"
                            placeholder="Seleccione un Departamento"
                            @input="llenarProvincia"
                          ></model-select>
                        </div>

                        <div class="col-12  col-lg-4" v-if="mapListaProvincia">
                          <div>
                            <label>Provincia:</label>
                          </div>
                          <model-select
                            :options="mapListaProvincia"
                            v-model="itemProvincia"
                            placeholder="Seleccione una Provincia"
                            @input="llenarDistrito"
                          ></model-select>
                        </div>

                        <div class="col-12  col-lg-4" v-if="mapListaDistrito">
                          <div>
                            <label>Distrito:</label>
                          </div>
                          <model-select
                            :options="mapListaDistrito"
                            v-model="itemDistrito"
                            placeholder="Seleccione un Distrito"
                          ></model-select>
                        </div>
                      </div>
                    </div>
                  </div>

                  <div class="alert alert-info mt-3" role="alert" v-html="descripcionDatosAdiconales"></div>
                  <div class="row" style="margin-top:5px">
                    <div class="form-group col-sm-12">
                      <div class="row">
                        <div class="form-group col-sm-3">
                          <div>
                            <label>Telfono:</label>
                          </div>
                          <b-input-group>
                            <b-form-input v-model="telefono" type="search"></b-form-input>
                          </b-input-group>
                        </div>
                        <div class="form-group col-sm-3">
                          <div>
                            <label>Celular:</label>
                          </div>
                          <b-input-group>
                            <b-form-input v-model="celular" @keypress="isNumber($event)" type="tel"></b-form-input>
                          </b-input-group>
                        </div>
                        <div class="form-group col-sm-6">
                          <div>
                            <label>Correo:</label>
                          </div>
                          <b-input-group>
                            <b-form-input v-model="correo" type="email"></b-form-input>
                          </b-input-group>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>

              </div>

            </b-card>
          </div>
          <br/>

          <div class="col-12">
            <b-card no-body style="padding:0px;"
                    header="Presentacin y formalidades de la solicitud (Art. 10 del Reglamento de la Ley N 27806)">
              <div class="alert alert-info " style="margin:0px" role="alert" v-html="descripcionPresentacion"></div>
            </b-card>
          </div>
          <br/>

          <div class="col-12">
            <b-card no-body style="padding:0px"
                    header="INFORMACIN QUE POSEE Y PRODUCE LA MUNICIPALIDAD DISTRITAL DE MIRAFLORES A CONSULTAR (*)">
              <b-form-textarea v-model="informacion" rows="6"
                               style="border:none;outline:none !important;box-shadow: none;"></b-form-textarea>
            </b-card>
          </div>
          <br/>

          <div class="col-12">
            <b-card header="ARCHIVO COMPLEMENTARIO A LA SOLICITUD">
              <div class="row">
                <div class="custom-file col-9 col-lg-10">
                  <input type="file" class="custom-file-input" id="customFileLang" lang="es" @change="onFileSelected">
                  <label class="custom-file-label" for="customFileLang"
                         placeholder="Seleccionar Archivo">{{ adjunto.nombre }}</label>
                </div>
                <div class="col-3  col-lg-2">
                  <b-button variant="light" class="btn-eliminar-SIP  d-lg-none" type="button" title="Eliminar"
                            @click.prevent="eliminar()">
                    <img width="17" :src="require(`../../assets/delete.png`)"/>
                  </b-button>
                  <b-button variant="light" class="btn-eliminar-SIP  d-none d-lg-block" type="button" title="Eliminar"
                            @click.prevent="eliminar()">Eliminar
                  </b-button>
                </div>
              </div>
            </b-card>
          </div>
          <br/>

          <div class="col-12">
            <b-card no-body style="padding:0px"
                    header="GERENCIA / SUBGERENCIA POSEEDORA DE LA INFORMACIN (SELECCIONES (TODAS) EN CASO DE NO CONOCERLO)">
              <div class="col-xs-12 col-md-12 col-lg-12" style="padding:0px">
                <div>
                  <multiselect :multiple="false" :close-on-select="true" :clear-on-select="false"
                               :preserve-search="false" :options="dataUnidades"
                               v-model="unidadOrigen" deselect-label="Quitar" select-label="Seleccionar" track-by="text"
                               label="text" placeholder="Seleccione una Unidad.">
                    <template slot="singleLabel" slot-scope="{ option }">
                      <strong>{{ option.text }}</strong>
                    </template>
                  </multiselect>
                </div>
              </div>
            </b-card>
          </div>
          <br/>
          <div class="col-12">
            <b-card no-body style="padding:0px" header="FORMA DE ENTREGA DE LA INFORMACIN (*)">
              <div class="row" style="margin-left:10px;margin-top:5px">
                <div class="form-group col-sm-8" style="padding:0px">
                  <b-form-radio-group style="margin-left:20px;margin-top:10px">
                    <b-form-radio name="radio-size" v-model="rdEntrega" value="317" size="sm">Copia Simple Formato
                    </b-form-radio>
                    <b-form-radio name="radio-size" v-model="rdEntrega" value="318" size="sm">Informacin en CD
                    </b-form-radio>
                    <b-form-radio name="radio-size" v-model="rdEntrega" value="319" size="sm">Informacin por correo
                      electrnico
                    </b-form-radio>
                  </b-form-radio-group>
                </div>
              </div>
              <div class="alert alert-info" style="margin:0px" role="alert" v-html="descripcionEntrega"></div>
            </b-card>
          </div>
          <br/>

          <div class="col-12">
            <b-card no-body style="padding:0px" header="REQUISITOS CONTENIDOS EN EL TUPA">
              <div class="alert alert-info" style="margin:0px" role="alert" v-html="descripcionRequisitos"></div>
            </b-card>
          </div>
          <br/>

          <div class="col-12">
            <b-card no-body style="padding:0px" header="OBSERVACIONES (OPCIONAL)">
              <b-form-textarea v-model="observaciones" rows="6"
                               style="border:none;outline:none !important;box-shadow: none;"></b-form-textarea>
            </b-card>
          </div>
          <br/>

          <div class="col-12">
            <div class="form-group  box-btn-registroAcceso">
              <b-button class="btn-registroAcceso" @click.prevent="IniciarTramite()" variant="primary">Enviar
                Solicitud
              </b-button>
            </div>
          </div>
          <br/>
        </div>
      </div>
    </div>
  </div>
</template>
<script>

import {ModelSelect} from "vue-search-select";
import Datepicker from "vuejs-datepicker";
import Util from "../../tools/util";
import axios from "axios";
import EventBus from "../../helpers/EventBus.js"
import Constantes from "../../store/constantes.js";
import Multiselect from 'vue-multiselect';
import moment from "moment";
import {es} from 'vuejs-datepicker/dist/locale';
import _ from "lodash"

export default {
  components: {Multiselect, Datepicker, ModelSelect},
  data() {
    return {
      descripcionTipoTramite: "",
      descripcionPresentacion: "",
      descripcionRequisitos: "",
      descripcionEntrega: "",
      descripcionDatosAdiconales: "",
      date: new Date,
      salto: `<br>`,
      tipoPer: 109,
      rdPersona: 109,
      rdTipoDoc: 31,
      rdEntrega: "",
      tipoDoc: 31,
      rSocial: "",
      nombre: "",
      aPaterno: "",
      aMaterno: "",
      nroDoc: "",
      domicilio: "",
      telefono: "",
      celular: "",
      correo: "",
      informacion: "",
      observaciones: "",
      dataUnidades: [],
      unidadOrigen: {},
      itemDepartamento: 14,
      itemProvincia: 1,
      itemDistrito: 1392,
      mapListaDepartamento: null,
      mapListaProvincia: null,
      mapListaDistrito: null,
      boolFormulario: false,
      boolVista: true,
      correoAcceso: "ACCESO.INFORMACION@MIRAFLORES.GOB.PE",
      texto: "",
      solicitud: "",
      anho: "",
      cDigitos: 0,
      adjunto: {nombre: null, archivo: null},
      cargado: false,
      nombArchivo: '',
      finish: false,
      enailEnviar: "",
      urlPdfDescargar: "",
    };
  },
  created() {
    window.document.title = "Registro"

    this.descripcionTipoTramite = "SOLICITUD DE ACCESO A LA INFORMACIN PBLICA CREADA U OBTENIDA POR LA ENTIDAD, QUE SE ENCUENTRE EN SU POSESIN O BAJO SU CONTROL" +
      "(Decreto Supremo N 164 -2020-PCM que aprueba el Procedimiento Administrativo Estandarizado de Acceso a la Informacin Pblica creada u obtenida por la entidad, que se encuentre en su posesin o bajo su control).";
    this.descripcionPresentacion = "<b>TEXTO:</b> 'La solicitud de acceso a la informacin pblica puede ser presentada por cualquier persona natural o jurdica ante la unidad de recepcin documentaria de la entidad, a travs de su Portal de Transparencia, a travs de una direccin electrnica establecida para tal fin o a travs de cualquier otro medio idneo que para tales efectos establezcan las Entidades " +
      "El uso de formato contenido en el Anexo del presente Reglamento es opcional para el/la solicitante, quien podr utilizar cualquier otro medio idneo para transmitir su solicitud que contenga la siguiente informacin: a. Nombres y apellidos completos, nmero del documento de identificacin que corresponda y domicilio. Tratndose de menores de edad no ser necesario consignar el nmero del documento de identidad; b. De ser el caso, nmero de telfono y/o correo electrnico; " +
      "c. En caso que la solicitud se presente en la unidad de recepcin documentaria de la Entidad, la solicitud debe contener firma del/de la solicitante o huella digital, de no saber firmar o estar impedido/a de hacerlo; d. Expresin concreta y precisa del pedido de informacin, as como cualquier otro dato que propicie la localizacin o facilite la bsqueda de la informacin solicitada; y e. En caso el/la solicitante conozca la dependencia que posea la informacin, deber indicarlo en la solicitud; " +
      " y f. Opcionalmente, la forma o modalidad en la que prefiere el/la solicitante que la Entidad le entregue la informacin de conformidad con lo dispuesto en la Ley. Si el/la solicitante no hubiese incluido el nombre del/de la funcionario/a responsable o lo hubiera hecho de forma incorrecta, las unidades de recepcin documentaria de las entidades debern canalizar la solicitud al/a la funcionario/a responsable." +
      "Las formalidades establecidas en este artculo tienen como finalidad garantizar la satisfaccin del derecho de acceso a la informacin pblica, por lo que deben interpretarse en forma favorable a la admisin y decisin final de las pretensiones del/de la solicitante'. (se incluy el lenguaje inclusivo en el texto).";
    this.descripcionRequisitos = "1. Solicitud presentada va formulario o documento que contenga la misma informacin. <br/>" +
      "2. De corresponder indicar nmero y fecha de comprobante de pago, en caso el pago se haya efectuado en la entidad. Si el pago se realiz en el Banco de la Nacin, adjuntar copia del comprobante de pago*. <br/>" +
      "* Este requisito se presenta posteriormente al ingreso de la solicitud. La entidad a partir del sexto da hbil de presentada la solicitud, pone a disposicin del ciudadano el costo de reproduccin de la informacin requerida a cancelar. <br/>" +
      "<b>Notas:</b> <br/>" +
      " - Solicitud de informacin dirigida al Responsable de Acceso a la Informacin Pblica. En caso de que este no hubiese sido designado, la solicitud se dirige al funcionario que tiene en su poder la informacin requerida o al superior inmediato. <br/>" +
      " - La solicitud puede ser presentada a travs del Portal de Transparencia de la Entidad, de forma personal ante la unidad de recepcin documentaria, o a travs de otros canales creados para tal fin. <br/>" +
      " - La liquidacin del costo de reproduccin que contiene la informacin requerida se pone a disposicin del solicitante a partir del sexto da de presentada la solicitud. En tal supuesto, el ciudadano se acerca a la entidad, cancela el monto, a efectos que la entidad efecte la reproduccin de la informacin requerida y pueda poner a disposicin la informacin dentro del plazo establecido legalmente. <br/>" +
      " - No se puede negar informacin cuando se solicite que esta sea entregada en una determinada forma o medio, siempre que el solicitante asuma el costo que suponga el pedido. <br/>" +
      " - En caso de presentacin de Recurso de Apelacin, el plazo mximo de presentacin es de 15 das hbiles, de conformidad al Precedente Vinculante emitido por el Tribunal de Transparencia y Acceso a la Informacin Pblica, segn Resolucin N 010300772020. El plazo mximo de respuesta es de 10 das hbiles, contados a partir de la admisibilidad del recurso por el Tribunal de Transparencia y Acceso a la Informacin Pblica, de conformidad con la Tercera Disposicin Complementaria Modificatoria del Decreto Supremo N 011-2018-JUS, que incorpora el artculo 16-B en el Reglamento de la Ley de Transparencia y Acceso a la Informacin Pblica. <br/>";
    this.descripcionEntrega = "(2) Se remitir la informacin por correo electrnico cuando los archivos adjuntos no sobrepasen el lmite de capacidad institucional; para lo cual, deber consignar un correo electrnico sin problemas tcnicos que impida la comunicacin con usted (por ejemplo: que el buzn est lleno, que est bloqueado temporalmente y/o posea restricciones para recibir o enviar correos institucionales, etc.). "

    this.descripcionDatosAdiconales = "Completar la siguiente informacin de contar con la misma (1): <br/>" +
      "(1) Su llenado es opcional; sin embargo, si requiere el envo de informacin por correo electrnico, es necesario completar dicho campo.";
    this.getUnidadOrganica();
    this.llenarDepartamento();
    this.mapListaProvincia = [];
    this.mapListaDistrito = []
    this.obtenerSolicitud(this.date);
    this.itemDepartamento = '';
    this.itemProvincia = '';
    this.itemDistrito = '';
    window.document.title = "Solicitud de Informacin Pblica";
  },
  filters: {
    upper: function (value) {
      return value.toUpperCase();
    }
  },
  methods: {
    regresar() {
      location.reload();
    },
    envioformulario() {
      this.boolVista = false;
      EventBus.$emit("loadershow", true);
      this.$recaptcha("accion").then((token) => {
        this.validarTokenGoogle(token)
      });
    },
    validarTokenGoogle(token) {
      const url = Constantes.rutaTareasComunes + "recaptcha/validacion";
      const params = {
        token: token,
        action: "login"
      }
      axios.post(url, params).then(response => {
        let data = response.data;
        if (data["json Google acceso"]) this.IniciarTramite();
        else this.$swal({
          icon: 'error',
          title: 'Error',
          text: 'Captcha error'
        });
      }).catch(e => console.warn(e))
    },
    IniciarTramite() {
      this.boolFormulario = true;
      if (this.validaFormulario()) {
        var fecha = this.customFormatter(this.date);
        var correoRegistro = this.correo.trim() === '' ? this.correoAcceso : this.correo;
        let dataPost = {};
        dataPost.nombUserRegistra = correoRegistro;
        dataPost.fecRegistra = fecha;
        if (this.rdPersona === 109) {
          dataPost.nomApellidoPaterno = this.aPaterno;
          dataPost.nomApellidoMaterno = this.aMaterno;
          dataPost.nomNombres = this.nombre;
        } else if (this.rdPersona === 110) {
          dataPost.nomNombres = this.rSocial;
        }
        dataPost.numDocumento = this.nroDoc;
        dataPost.numTelefono = this.telefono;
        if (this.celular != null && this.celular.length > 0) dataPost.numCelular = this.celular;
        dataPost.desEmail = this.correo;
        dataPost.desDetalleSolicitud = this.informacion;
        dataPost.ideTipoPersona = this.rdPersona;
        dataPost.ideTipoDocumento = this.rdTipoDoc;
        if (this.itemDepartamento && this.itemDepartamento > 0) {
          dataPost.idDepartamento = this.itemDepartamento;
          dataPost.desDepartamento = this.mapListaDepartamento.find(element => element.value === this.itemDepartamento).text;
        }
        if (this.itemProvincia && this.itemProvincia > 0) {
          dataPost.idProvincia = this.itemProvincia;
          dataPost.desProvincia = this.mapListaProvincia.find(element => element.value === this.itemProvincia).text;
        }
        if (this.itemDistrito && this.itemDistrito > 0) {
          dataPost.idDistrito = this.itemDistrito;
          dataPost.desDistrito = this.mapListaDistrito.find(element => element.value === this.itemDistrito).text;
        }
        dataPost.domicilio = this.domicilio;
        dataPost.desUsuRegistra = correoRegistro;
        dataPost.desObservacion = this.observaciones;
        if (this.unidadOrigen.value !== undefined && this.unidadOrigen.value !== 0) dataPost.desUnidadSel = this.unidadOrigen.text;
        dataPost.idTipoEntrega = this.rdEntrega;

        var dataDocumento = new FormData();
        if (this.adjunto.nombre != null) {
          dataDocumento.append('documento', this.adjunto.archivo);
        }
        dataDocumento.append('dataPost', JSON.stringify(dataPost));
        this.$swal({
          title: "Guardando solicitud...",
          onOpen: () => {
            this.$swal.showLoading();
          }
        });
        axios.post(Constantes.rutaAtencion + 'grabarsolicitudinfpublica', dataDocumento)
          .then(response => {
            EventBus.$emit("loadershow", false);
            this.finish = true
            this.idSolicitud = response.data.data.numRegistro
            this.urlPdfDescargar = response.data.data.urlConstacia
            this.$swal.fire({
              icon: 'success',
              title: 'Solicitud procesada con xito',
              showConfirmButton: false,
              timer: 1500
            })
          }).catch(e => this.$swal({
            icon: 'info',
            text: 'Error en registro de solicitud.'
          })
        );
      } else {
        this.$swal({
          icon: 'info',
          text: this.texto
        });
        EventBus.$emit("loadershow", false);
        this.boolVista = true;
      }
    },
    getUnidadOrganica() {
      axios.get(Constantes.rutaTramite + 'unidadorganica/-')
        .then(response => {
          var i = 0;
          while (i < response.data.data.length) {
            var unidadorganica = {};
            unidadorganica.value = response.data.data[i].uniorgcod;
            unidadorganica.text = response.data.data[i].uniorgnom;
            this.dataUnidades.push(unidadorganica);
            i++;
          }
          this.unidadOrigen = this.dataUnidades[0];
        })
        .catch(e => console.log(e))
    },
    llenarDepartamento() {
      axios.get(Constantes.API_PERSONA + '/ubigeo/1/0/0/0')
        .then(response => {
          let data = response.data.data;
          this.mapListaDepartamento = [];
          let i = 0;
          while (i < data.length) {
            var departamento = {};
            departamento.value = data[i].indDepartam;
            departamento.text = data[i].desNombre;
            this.mapListaDepartamento.push(departamento);
            i++;
          }
        })
        .catch(e => console.log(e))
    },
    llenarProvincia() {
      axios.get(Constantes.API_PERSONA + '/ubigeo/1/' + this.itemDepartamento + '/0/0')
        .then(response => {
          let data = response.data.data;
          this.mapListaProvincia = []
          let i = 0;
          while (i < data.length) {
            var provincia = {};
            provincia.value = data[i].indProvincia;
            provincia.text = data[i].desNombre;
            this.mapListaProvincia.push(provincia);
            i++;
          }
        })
        .catch(e => console.log(e))
    },
    llenarDistrito() {
      axios.get(Constantes.API_PERSONA + '/ubigeo/1/' + this.itemDepartamento + '/' + this.itemProvincia + '/0')
        .then(response => {
          let data = response.data.data;
          this.mapListaDistrito = []
          console.log(data)
          let i = 0;
          while (i < data.length) {
            var distrito = {};
            distrito.value = data[i].ideUbigeo;
            distrito.text = data[i].desNombre;
            this.mapListaDistrito.push(distrito);
            i++;
          }
        })
        .catch(e => console.log(e))
    },
    obtenerSolicitud(date) {
      this.anho = date.getFullYear();
      axios.get(Constantes.rutaAtencion + 'obtener-solicitudmaxima/324').then(response => {
        this.solicitud = response.data.data;
      }).catch(e => console.log("Error al obtener nmero de solicitud."));
    },
    getExtensionFile(filePath) {
      var lastDot = filePath.lastIndexOf('.');
      var fileName = filePath.substring(0, lastDot);
      var ext = filePath.substring(lastDot + 1);
      return ext;
    },
    onFileSelected($event) {
      var extension = this.getExtensionFile(event.target.files[0].name);
      if (extension == 'pdf') {
        this.adjunto = {};
        this.adjunto.nombre = event.target.files[0].name;
        this.adjunto.archivo = event.target.files[0];
        this.nombArchivo = this.adjunto.nombre;
        this.cargado = true;
      }
    },
    eliminar() {
      this.adjunto = {};
      this.cargado = false;
      this.nombArchivo = '';
    },
    customFormatter(date) {
      return moment(date).format('YYYY-MM-DD');
    },
    validaFormulario() {
      this.texto = '';
      if (this.rdTipoDoc == 31 && !((this.nroDoc).length == 8)) {
        this.boolFormulario = false;
        this.texto = 'Nmero de dgitos incorrecto.';
      }
      if (this.rdTipoDoc == 32 && !((this.nroDoc).length == 11)) {
        this.boolFormulario = false;
        this.texto = 'Nmero de dgitos incorrecto.';
      }
      if (this.rdTipoDoc == 33 && !((this.nroDoc).length >= 8 && (this.nroDoc).length <= 12)) {
        this.boolFormulario = false;
        this.texto = 'Nmero de dgitos incorrecto.';
      }
      if (this.rdPersona == 109) {
        if (this.nombre.trim() == '' || this.aPaterno.trim() == '' || this.nroDoc.trim() == '' || this.informacion.trim() == '' || this.domicilio.trim() == '' || this.rdEntrega == '') {
          this.boolFormulario = false;
          this.texto = 'Favor de ingresar los campos obligatorios (*).';
        } else {
          this.boolFormulario = true;
        }
      } else if (this.rdPersona == 110) {
        if (this.rSocial.trim() == '' || this.nroDoc.trim() == '' || this.informacion.trim() == '' || this.domicilio.trim() == '' || this.rdEntrega == '') {
          this.boolFormulario = false;
          this.texto = 'Favor de ingresar los campos obligatorios (*).';
        } else {
          this.boolFormulario = true;
        }
      }
      if (this.correo != '') {
        if (!this.validEmail(this.correo.trim())) {
          this.boolFormulario = false;
          if (this.texto == '') this.texto = 'Correo ingresado no vlido.';
          else this.texto = this.texto + ' Correo ingresado no vlido.';
        } else {
          if (this.boolFormulario == true) this.boolFormulario = true;
        }
      }
      if (this.nroDoc.trim() != '') {
        this.cDigitos = 0;
        if (this.rdTipoDoc == 31 && (this.nroDoc.length > 8 || this.nroDoc.length < 8)) {
          this.cDigitos = 8;
          this.boolFormulario = false;
          if (this.texto == '') this.texto = 'Cantidad de dgitos (' + this.cDigitos + ') de nmero de documento incorrecto para el tipo seleccionado.';
          else this.texto = this.texto + ' Cantidad de dgitos (' + this.cDigitos + ') de nmero de documento incorrecto para el tipo seleccionado.';
        } else if (this.rdTipoDoc == 32 && (this.nroDoc.length > 11 || this.nroDoc.length < 11)) {
          this.cDigitos = 11;
          this.boolFormulario = false;
          if (this.texto == '') this.texto = 'Cantidad de dgitos (' + this.cDigitos + ') de nmero de documento incorrecto para el tipo seleccionado.';
          else this.texto = this.texto + ' Cantidad de dgitos (' + this.cDigitos + ') de nmero de documento incorrecto para el tipo seleccionado.';
        } else if (this.rdTipoDoc == 33 && (this.nroDoc.length > 12 || this.nroDoc.length < 8)) {
          this.cDigitos = 12;
          this.boolFormulario = false;
          if (this.texto == '') this.texto = 'Cantidad de dgitos (' + this.cDigitos + ') de nmero de documento incorrecto para el tipo seleccionado.';
          else this.texto = this.texto + ' Cantidad de dgitos (' + this.cDigitos + ') de nmero de documento incorrecto para el tipo seleccionado.';
        }
      }
      if (this.rdEntrega == 319 && this.correo == '') {
        this.boolFormulario = false;
        if (this.texto == '') this.texto = 'Es necesario el correo electrnico para el tipo de entrega seleccionado.';
        else this.texto = this.texto + ' Es necesario el correo electrnico para el tipo de entrega seleccionado.';
      }
      return this.boolFormulario;
      //this.envioformulario();
    },
    isNumber: function (evt) {
      evt = (evt) ? evt : window.event;
      var charCode = (evt.which) ? evt.which : evt.keyCode;
      if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
        evt.preventDefault();
        ;
      } else {
        return true;
      }
    },
    validEmail: function (email) {
      var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(email);
    },
    enviarCorreo() {
      this.$swal({
        title: "Enviando correo...",
        onOpen: () => {
          this.$swal.showLoading();
        }
      });
      const url = Constantes.rutaAtencion + "enviar-correo-acceso-informacion"
      axios.get(url, {
        params: {
          pdfUrl: this.urlPdfDescargar,
          correo: this.enailEnviar,
          idSolicitud: this.idSolicitud

        },
      }).then((response) => {
        this.$swal.fire({
          icon: 'success',
          title: 'Correo enviado con xito',
          showConfirmButton: false,
          timer: 1500
        })
      });
    },

  }
};
</script>
<style lang="scss" scoped>
