  border: solid 1px #e9eff9 !important;
  box-shadow: none !important;
  width: 120px !important;
  height: 35px !important;
  font-size: 14px;
}

.descripcion-content {
  height: 100%;
  position: relative;
  margin-bottom: 100px;

  .mobile-block {
    display: none;
  }

  .top-title {
    padding-top: 24px;
