    text-transform: capitalize;
  }

  .full {
    display: flex;
    justify-content: flex-end;
    align-items: center;

    .conten-icon {
      padding-left: 3px;
    }

    &.full2 {
      height: initial;

      .conten-letter {
        span.change p.nom {
          white-space: initial;
          overflow: inherit;
          text-overflow: initial;
        }

        .change {
          padding-left: 0px;
          display: flex;
          align-items: center;

          .closee {
            top: inherit;
            justify-content: center;
          }

          img {
            position: relative;
            left: initial;
            top: initial;
            padding-left: 5px;
            padding-right: 5px;
          }

          a {
            display: flex;
            align-items: center;
            justify-content: space-between;
          }
        }
      }
    }
  }

  &:hover {
    .closee {
      display: block !important;
      background: #fff;
    }

    .conten-letter {
      position: relative;

      input {
        height: 18px;
        cursor: pointer;
      }

      span {
        display: inline-block;
        cursor: pointer;
      }
    }
  }

  .conten-letter {
    position: relative;

    span.change {
      display: inline-block;
      position: relative;
      padding-left: 42px;

      p.nom {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        margin-bottom: 0px;
        max-width: 100px;
      }

      img {
        position: absolute;
        left: 4px;
        top: 0px;
      }
    }

    .change {
      padding-top: 5px;
      padding-bottom: 5px;
      font-size: 12px;
      line-height: normal;
      padding-left: 10px;
      padding-right: 10px;
      border-radius: 10px;
      background: #fff;
      color: #9c9c9c;
      border: solid 1px #ddd;

      & ~ input {
        display: none;
      }
    }
  }

  .content-requi {
    margin-top: 15px;
    padding-bottom: 15px;

    .requi-title {
      padding-left: 25px;
      padding-top: 20px;
      padding-right: 25px;

      label {
        margin-bottom: 0px;
        padding-bottom: 0px;
      }
    }

    ul {
      padding-left: 20px;
      list-style: none;
      padding-top: 25px;
      padding-right: 20px;

      li {
        flex-flow: row;
        align-items: flex-start;
        display: flex;
        width: 100%;
        margin-bottom: 12px;

        .left-img {
          margin-right: 10px;
          width: 25px;

          img {
            max-width: 20px;
          }
        }

        &:hover {
          .closee {
            display: block !important;
            background: #fff;
          }

          .conten-letter {
            position: relative;

            input {
              height: 18px;
              cursor: pointer;
            }

            span {
              display: inline-block;
              cursor: pointer;
            }
          }
        }

        .conten-adj {
          width: 385px;
          line-height: normal;
        }

        .full {
          max-width: 193px;
          width: 100%;
          text-align: right;
          height: 20px;

          &.full2 {
            max-width: inherit;
          }
        }

        .conten-letter {
          position: relative;

          span.change {
            display: inline-block;
            position: relative;
            padding-left: 42px;

            p.nom {
              white-space: nowrap;
              overflow: hidden;
              text-overflow: ellipsis;
              margin-bottom: 0px;
              max-width: 100px;
            }

