              left: 4px;
              top: 0px;
            }
          }

          .change {
            padding-top: 5px;
            padding-bottom: 5px;
            font-size: 12px;
            line-height: normal;
            padding-left: 10px;
            padding-right: 10px;
            border-radius: 10px;
            background: #fff;
            color: #9c9c9c;
            border: solid 1px #ddd;

            & ~ input {
              display: none;
            }
