        }

        .conten-icon {
          span {
            width: 10px;
            height: 10px;
            border-radius: 50%;
            display: inline-block;

            &.er {
              background: #ff6464;
            }

            &.su {
              background: #bed730;
            }

            &.pe {
              background: #989898;
            }
