        }
      }

      &.full-list {
        padding-left: 26px;
        padding-top: 5px;

        li {
          .full {
            width: 100%;
            text-align: left;
          }

          p.nom {
            width: 100%;
            max-width: 100% !important;
          }
        }
      }
    }
  }

  .custom-file-input {
    position: absolute;
    left: -2px;
    top: -4px;
    cursor: pointer;
    height: 27px;
  }

  .add {
    .custom-file-input {
      position: absolute;
      right: 0;
      cursor: pointer;
      height: 35px;
      width: 181px;
      left: inherit;
      top: -6px;
    }
