}

$custom-file-text: (
  en: "Browse",
  es: "Elegir"
);
@media only screen and (max-width: 600px) {
  .descripcion-content {
    ul {
      margin-bottom: 0px;
    }

    .mobile-block {
      padding-top: 40px;
      padding-bottom: 60px;
      text-align: center;
      display: block;
    }

    .top-title {
      flex-direction: column-reverse;

      > div {
        width: 100% !important;
      }

      h5 {
        width: 100%;
      }
    }

    .mobile-hide {
      display: none;
    }


  }
}
</style>



